import React, { Component } from 'react'
import { Container, Draggable } from 'react-smooth-dnd'
import { OptionComponents } from './emailComponents'

const EmailComponent = ({ configurations, key }) => (
    <Draggable key={key}>
        <div className="draggable-item">
            <OptionComponents id={configurations.id}/>
        </div>
    </Draggable>
)

export class OptionsContainer extends Component {
    onDragStart = () => this.props.doChangeDragCursor(true)
    onDragEnd = () => this.props.doChangeDragCursor(false)
    doDragCursor = (doIt) => {
        const target = document.getElementsByClassName('app-body')
        if (doIt) {
            console.log({ target })
        } else {
        }
    }

    render = () => {
        const { components } = this.props
        return (
            <div className={'options-wrapper'} >
                <Container
                    groupName="email-container"
                    behaviour="copy"
                    getChildPayload={index => components[index]}
                    onDragStart={this.onDragStart}
                    onDragEnd={this.onDragEnd}
                    className={'options-container'}
                >
                    {components.map((configurations, index) =>
                        <EmailComponent configurations={configurations} key={index} />)}
                </Container>
            </div>
        )
    }
}

export default OptionsContainer




