import React from 'react'
import { ImageTextTopIcon } from './imageTextTop'

export const DoubleImageTextIcon = () => (
    <div className={'email-component double-image-text'} style={{ display: 'flex' }}>
        <div className={'email-component-stacks'}>
            <ImageTextTopIcon />
            <ImageTextTopIcon />
        </div>
    </div>
)

export const DoubleImageText = ({ options, style }) => (
    <div className={'canvas-component double-image-text'} style={style}>
        { options.map(( item, index ) => <ImageTextTop {...item} key={index}/> )}
    </div>
)

const ImageTextTop = ({ image, descript, key }) => (
    <div className={''} key={key}>
        <img src={image.src} style={image.style}/>
        <p style={descript.style}>{descript.text}</p>
    </div>
)

export default { DoubleImageText, DoubleImageTextIcon }



