import React from 'react'
import { ImageTextTopIcon } from './imageTextTop'

export const TripleImageTextIcon = () => (
    <div className={'email-component triple-image-text'}>
        <div className={'email-component-stacks'}>
            <ImageTextTopIcon />
            <ImageTextTopIcon />
            <ImageTextTopIcon />
        </div>
    </div>
)

export const TripleImageText = ({ options, style }) => (
    <div className={'canvas-component triple-image-text'} style={style}>
        { options.map(( item, index ) => <ImageTextTop {...item} key={index}/> )}
    </div>
)

const ImageTextTop = ({ image, descript, key }) => (
    <div className={''} key={key}>
        <img src={image.src} style={image.style}/>
        <p style={descript.style}>{descript.text}</p>
    </div>
)

export default { TripleImageText, TripleImageTextIcon }



