import React from 'react'
import titleSVG from '../../../img/icons/title.svg'

export const TitleIcon = () => (
    <div className={'email-component title'}>
        <img src={titleSVG} className={'title-icon'}/>
    </div>
)

export const Title = ({ title, subtitle, style }) => (
    <div className={'canvas-component title'} style={style}>
        <h1 style={title.style}>{title.text}</h1>
        <h4 style={subtitle.style}>{subtitle.text}</h4>
    </div>
)

export default { Title, TitleIcon }



